/* Reset browser defaults */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	vertical-align: baseline;
}

/* End Reset browser defaults   */
.presentation-box {
    background-color: #2E3031;
    color: #F5F7FA;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 5%;
    gap: 20px;
    margin-top: 8%;
}

/* This is the new parent container for the image and caption */
.presentation-box-img {
    flex-shrink: 0;
    flex: 1; /* Allow this container to grow */
    display: flex; /* Make it a flex container itself */
    flex-direction: column; /* Stack the image and caption vertically */
    justify-content: center; /* Center them vertically within this container */
    height: 100%; /* Make this container fill the full height */
}

/* The image itself needs to be flexible */
/* The image itself needs to be flexible */
.intro-img {
    display: block;
    max-width: 100%; /* Use max-width to allow the image to shrink */
    height: auto; /* Use auto to maintain the aspect ratio */
    object-fit: contain; /* CRUCIAL: This will make the image fit inside the container without being cut */
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}
.image-caption {
    font-size: 0.7em;
    font-style: italic;
    color: #BCBCBC;
    text-align: left;
    margin-top: 4px;
    /* You might need to adjust margin or padding here to make it fit */
}

.intro-text {
    max-width: 60ch;
    text-align: center;
}

/* Style to highlight bold words */
.intro-text b {
    color: #EF5350;
    font-weight: 600;
}

/* On screens wider than 768px (tablets and desktops) */
@media (min-width: 768px) {
    .presentation-box {
        flex-direction: row;
        gap: 2%;
        align-items: stretch; /* CHANGED: This is the most important change. It makes both children stretch to the full height of the parent flexbox. */
    }

    .presentation-box-img, .intro-text {
        flex: 1;
        /* The image container and the text container now both stretch to fill the height */
    }

    .intro-text {
        text-align: left;
    }
}
