/* START Copy Button Tooltip Syle*/
.material-symbols-outlined {
  cursor: pointer;
  position: relative;
  display: inline-block;
  margin: 3%;
  color: #2E2E2E;
  float: right;
  font-variation-settings:
    'FILL' 0,
    'wght' 400,
    'GRAD' 0,
    'opsz' 48;
}


.material-symbols-outlined .tooltiptext {
  visibility: hidden;
  opacity: 0;
  width: max-content;
  max-width: 200px;
  background-color: #0f1723;   /* dark navy background */
  color: #fff;
  font-weight: 500;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  text-align: center;
  padding: 6px 10px;
  border-radius: 8px;
  position: absolute;
  z-index: 10;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.8rem;
  line-height: 1.4;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.15);
  transition: opacity 0.25s ease, visibility 0.25s ease, transform 0.25s ease;
}

.material-symbols-outlined .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  border-width: 6px;
  border-style: solid;
  border-color: #0f1723 transparent transparent transparent;
}


.material-symbols-outlined:hover .tooltiptext,
.material-symbols-outlined:focus-within .tooltiptext {
  visibility: visible;
  opacity: 1;
  transform: translateX(-50%) translateY(-4px); /* subtle lift effect */
}

/* END Copy Button Tooltip Syle*/
